<script>

  import home from '../../home/home/home'
  import mine from '../../mine/mine/mine'
  export default {
    name: 'root',
    data () {
      return {
        selected: '首页'
//        tabbarTitles: ['首页', '团购预约', '购物车', '我的']

      }
    },
    created () {

    },
    components: {
      home,
      mine
    },
    watch: {
      selected (newVal, oldVal) {
        if (newVal === '首页') {
          this.$router.push({path: '/home'})
        } else if (newVal === '购物车') {
          this.$router.push({path: '/ShoppingCart'})
        } else if (newVal === '我的') {
          this.$router.push({path: '/Mine'})
        } else {
          this.$router.push({path: '/home'})
        }
      }
    }
  }
</script>

